<template>
  <div class="song-list">
    <ul>
      <li @click="selectItem(song, index)" class="item" v-for="(song, index) in songs" :key = index>
        <!-- 奖杯部分 -->
        <div class="sort" v-show="rank">
          <!-- 由于前面3个是奖杯，后面是数字，所以class这儿使用函数的方式 -->
          <span :class="getRankCls(index)" v-text="getRankText(index)"></span>
        </div>
        <div class="content">
          <h2 class="name">{{song.name}}</h2>
          <p class="desc">{{getDesc(song)}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      songs: {
        type: Array,
        default: function () { return []; }
      },
      rank: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      getDesc (song) {
        return `${song.singer} . ${song.album}`;
      },
      selectItem (item, index) {
        this.$emit('select', item, index);
      },
      getRankCls (index) {
        if (index <= 2) {
          return `icon icon${index}`;
        } else {
          return 'text';
        }
      },
      getRankText (index) {
        if (index > 2) {
          return index + 1;
        }
      }
    }
  };
</script>

<style lang="less" rel="stylesheet/less">
  @import "../../common/less/variable";
  @import "../../common/less/mixin";

  .song-list {
    .item {
      display: flex;
      align-items: center;
      box-sizing: border-box;
      height: 64px;
      font-size: @font-size-medium;
      .sort {
        flex: 0 0 25px;
        width: 25px;
        margin-right: 30px;
        text-align: center;
        .icon {
          display: inline-block;
          width: 25px;
          height: 24px;
          background-size: 25px 24px;
          &.icon0 {
            .bg-image('first');
          }
          &.icon1 {
            .bg-image('second');
          }
          &.icon2 {
            .bg-image('third');
          }
        }
        .text {
          color: @color-theme;
          font-size: @font-size-large;
        }
      }
      .content {
        flex: 1;
        line-height: 20px;
        overflow: hidden;
        .name {
          .no-wrap();
          color: @color-text;
        }
        .desc {
          .no-wrap();
          margin-top: 4px;
          color: @color-text-d;
        }
      }
    }
  }
</style>



